body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

/*.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}*/

html,
body {
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

header {
  background: url(../img/фон.jpg) center top / cover no-repeat;
  padding-bottom: 180px;
  margin-bottom: -120px;
}

.headerTop {
  background-color: #fff;
  border-radius: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.headerTop .logo {
  width: 160px;
  padding: 3px;
}

.headerTop .logo img {
  padding-left: 10px;
}

.headerTop .menu {
  width: calc(-60%);
  display: flex;
  justify-content: flex-end;
}

.headerTop .menu nav {
  display: flex;
  align-items: center;
}

.headerTop .menu nav li a {
  color: #001d4f;
  font-size: 15px;
  padding: 0 20px;
  font-weight: 600;
}

.registration {
  color: #fff;
  font-size: 15px;
  padding: 15px 34px;
  background-color: #fba0c6;
  border-radius: 22px;
}

.logo-2 {
  margin-top: 30px;
  display: flex;
  align-items: center;
}

.logo-2 img {
  width: 419px;
  padding-right: 20px;
}

.logo-2 span {
  color: #e93887;
  font-size: 47px;
  font-weight: 700;
  margin-top: 21px;
}

h1 {
  color: #000000;
  font-size: 47px;
  font-weight: 700;
  margin-top: 10px;
}

.title {
  color: #172e56;
  font-family: "Open Sans - Semi Bold";
  font-size: 23px;
  font-weight: 400;
  line-height: 33px;
  margin: 20px 0;
}

.timer {
  width: 570px;
  padding-bottom: 20px;
  border: 1px solid #fff;
  border-radius: 5px;
  margin: 20px 0;
  background-color: #f9f1e6;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.start {
  color: #e93887;
  font-family: "Open Sans - Semi Bold";
  font-size: 26px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 10px;
  padding: 10px 0;
}

.count {
  display: flex;
  border-top: 1px solid #fff;
  justify-content: center;
}

.date {
  padding: 5px;
}

.name {
  color: #001d4f;
  text-align: center;
  padding-bottom: 10px;
  font-size: 15px;
  font-weight: 600;
}

.pos {
  position: relative;
  z-index: 1;
}

.left {
  position: absolute;
  width: 26px;
  height: 48px;
  background-color: #fff;
  top: 5px;
  left: 7px;
  border-radius: 7px;
  z-index: -1;
}

.right {
  position: absolute;
  width: 26px;
  height: 48px;
  background-color: #fff;
  top: 5px;
  left: 7px;
  border-radius: 7px;
  z-index: -1;
  left: 35px;
}

.numb {
  color: #001d4f;
  font-family: "Bebas Neue Bold";
  font-size: 43px;
  letter-spacing: 7px;
  font-weight: 600;
  padding: 4px 10px;
  border-radius: 10px;
  z-index: 0;
}

.reg {
  align-self: flex-end;
  margin-left: 10px;
}

.info {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-bottom: 100px;
  justify-content: center;
}

.info .info-block {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(25% - 30px);
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 12px 12px 12px -9px #e5e5e5;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info .info-block .label {
  background: url(../img/love.jpg) center top no-repeat;
  color: #fba0c6;
  font-family: "Open Sans - Semi Bold";
  font-size: 49px;
  font-weight: 600;
  text-align: center;
  margin-top: 10px;
  padding: 15px 10px;
}

.info .info-block .text {
  width: 80%;
  border-top: 1px solid #d4d9e1;
}

.info .info-block .text h4 {
  color: #001d4f;
  font-family: "Open Sans - Semi Bold";
  font-size: 23px;
  font-weight: 600;
  text-align: center;
  padding: 10px 0;
}

.info .info-block .text div {
  color: #001d4f;
  font-family: "Open Sans";
  font-size: 15px;
  font-weight: 400;
  text-align: center;
  padding: 10px 0 20px;
}

.about {
  color: #001d4f;
  font-family: "Open Sans - Semi Bold";
  font-size: 23px;
  font-weight: 400;
  line-height: 39.24px;
  text-align: center;
}

.line {
  width: 71px;
  height: 7px;
  background-color: #fba0c6;
  border-radius: 4px;
  margin: 20px auto;
}

.content {
  margin-bottom: 150px;
}

.autor_title {
  width: calc(66.66666666666667% - 30px);
  margin: 0 auto;
  text-align: center;
}

.autor_main {
  margin-top: 25px;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.autor_main .autor_img {
  position: relative;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(41.66666666666667% - 30px);
}

.autor_main .autor_img .img {
  position: absolute;
  top: 0;
  right: 12px;
  width: 568px;
  height: 607px;
  background-image: url(../img/autor.jpg);
  background-repeat: no-repeat;
}

.autor_info {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(58.333333333333336% - 30px);
  margin-top: 92px;
  position: relative;
  background: #fffefe;
  border-radius: 10px;
  box-shadow: 10px 5px 20px 0 #dfdfdf;
}

.autor_info .frame {
  width: 92%;
  height: 300px;
  border: 7px solid #ffeac5;
  border-radius: 5px;
  position: absolute;
  top: -14px;
  left: -14px;
  z-index: -10;
}

.autor_info:after {
  content: "";
  width: 50px;
  height: 89px;
  background: url(../img/left-decor.png);
  position: absolute;
  left: 0;
  top: 270px;
  transform: translateX(-100%);
}

.autor_info:before {
  content: "";
  width: 108px;
  height: 110px;
  background: url(../img/right-decor.png);
  position: absolute;
  top: -36px;
  right: -4px;
  z-index: 1;
}

.autor_info .autor_info_text {
  padding-bottom: 40px;
}

.autor_info h3 {
  color: #001d4f;
  font-family: "Open Sans - Semi Bold";
  font-size: 23px;
  font-weight: 600;
  line-height: 39px;
  padding: 10px;
}

.autor_info p {
  color: #001d4f;
  font-family: "Open Sans";
  font-size: 15px;
  font-weight: 400;
  line-height: 19px;
  padding: 15px;
}

.autor_info .btn {
  position: absolute;
  left: 30px;
  bottom: 0;
  transform: translateY(50%);
  box-shadow: inset 0 -10px 15px #fffbfb, inset 0 9px 15px #dfdfdf;
  border-radius: 30px;
  padding: 9px;
  background: #fff;
}

.autor_info .btn div {
  padding-left: 38px;
  padding-right: 38px;
  color: #fff;
  border-radius: 26px;
  background: #fba0c6;
  font-size: 15px;
  border: 1px solid #fba0c6;
  padding: 17px 50px;
  cursor: pointer;
}

.certificates {
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.certificates .certificate_item {
  display: flex;
  justify-content: flex-end;
  padding-top: 15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(25% - 30px);
}

.certificates .certificate_item .certificate_img {
  position: relative;
}

.certificates .certificate_item .certificate_img img {
  cursor: pointer;
}

.certificates .certificate_item .certificate_img img:hover {
  transform: scale(1.1);
}

.certificates .certificate_item .certificate_img .certificate_frame {
  position: absolute;
  top: -12px;
  left: -15px;
  border: 7px solid #ffeac5;
  border-radius: 10px;
  width: 90%;
  height: 90%;
  z-index: -10;
}

.radius {
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: #000;
  margin-top: 30px;
}

.slick-active .radius {
  background-color: #bebebe;
}

.burger {
  display: none;
  cursor: pointer;
}

.burger-menu {
  width: 100%;
  position: absolute;
  bottom: -208px;
  right: 0;
  background-color: #fff;
  border-radius: 10px;
  display: none;
}

.burger-menu li {
  padding: 10px;
  cursor: pointer;
}

.burger-menu li a {
  color: #000;
}

.off {
  display: block;
}

.autor_img-adaptive {
  display: none;
}

.btn-adaptive {
  display: none;
}

@media screen and (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  h1 {
    font-size: 37px;
  }

  .autor_title {
    width: calc(83.33333333333334% - 30px);
  }

  .autor_main .autor_img {
    width: calc(50% - 30px);
  }

  .autor_info {
    width: calc(50% - 30px);
  }

  .certificates .certificate_item {
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 815px) {
  .headerTop .menu {
    display: none;
  }

  .headerTop .burger {
    display: block;
    margin-right: 20px;
  }

  .logo-2 {
    flex-direction: column;
  }

  .logo-2 img {
    order: 2;
    margin: 10px;
  }

  .content {
    margin-bottom: 50px;
  }

  .autor_main {
    justify-content: flex-start;
    margin-left: 30px;
  }

  .autor_main .autor_img {
    display: none;
  }

  .autor_main .autor_img-adaptive {
    display: block;
    margin-top: -30px;
    z-index: 10;
    width: calc(66.66666666666667% - 30px);
  }

  .autor_main .autor_info {
    width: calc(91.66666666666667% - 30px);
  }

  .autor_main .autor_info:after {
    content: "";
    width: 76px;
    height: 48px;
    background: url(../img/bottom-decor.png);
    position: absolute;
    left: 80%;
    top: 100.1%;
  }

  .autor_main .autor_info .frame {
    height: 90%;
  }

  .btn {
    display: none;
  }

  .btn-adaptive {
    display: flex;
    justify-content: center;
  }

  .btn-adaptive div {
    text-align: center;
    margin-bottom: 70px;
    width: 30%;
    display: block;
    color: #fff;
    font-size: 15px;
    padding: 15px 34px;
    background-color: #fba0c6;
    border-radius: 22px;
  }
}

@media screen and (max-width: 720px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }

  h1 {
    font-size: 30px;
    text-align: center;
  }

  .title {
    text-align: center;
    font-size: 20px;
  }

  .timer {
    width: 100%;
  }

  .count {
    flex-wrap: wrap;
  }

  .reg {
    width: 70%;
    text-align: center;
    margin-top: 20px;
  }

  .info .info-block {
    width: calc(41.66666666666667% - 30px);
  }

  .certificates .certificate_item {
    width: calc(41.66666666666667% - 30px);
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }

  .info .info-block {
    width: calc(100% - 30px);
  }

  .info .info-block {
    display: flex;
    flex-direction: row;
    padding: 15px;
  }

  .info .info-block .text {
    border-top: none;
    border-left: 1px solid #d4d9e1;
  }

  .info .info-block .text h4 {
    font-size: 20px;
  }

  .info .info-block .label {
    font-size: 32px;
    margin-right: 5px;
  }

  .autor_info h3 {
    font-size: 18px;
  }

  .autor_info p {
    line-height: 15px;
    font-size: 13px;
  }
}

@media screen and (max-width: 500px) {
  .btn-adaptive div {
    width: 70%;
  }
}

@media screen and (max-width: 380px) {
  .container {
    padding-left: 5px;
    padding-right: 5px;
  }
}